<template>
  <div class="home">
    <div class="header">
      <div class="logo" @click="onClickLeft">
        <van-icon name="arrow-left" class="icon" />
        <span>分类</span>
      </div>
      <van-tabs
        class="top"
        type="card"
        @change="change"
        color="#ed424b"
        background="#fff"
        swipeable
        v-model="isSex"
      >
        <van-tab title="男生">
          <div class="classify">
            <div
              class="classify-items clearfix"
              v-for="(item,index) in majorData.male"
              :key="index"
            >
              <div class="classify-top clearfix">
                <div class="classify-title fl">{{item.name}}</div>
                <div class="classify-works fl">共{{item.bookCount}}本作品</div>
              </div>
              <div class="classify-down">
                <ul class="clearfix">
                  <li
                    v-for="(item2,index2) in item.son"
                    :key="index2"
                    @click="goPage(item2,item,isSex)"
                  >{{item2}}</li>
                </ul>
              </div>
            </div>
          </div>
        </van-tab>
        <van-tab title="女生">
          <div class="classify">
            <div
              class="classify-items clearfix"
              v-for="(item,index) in majorData.female"
              :key="index"
            >
              <div class="classify-top clearfix">
                <div class="classify-title fl">{{item.name}}</div>
                <div class="classify-works fl">共{{item.bookCount}}本作品</div>
              </div>
              <div class="classify-down">
                <ul class="clearfix">
                  <li
                    v-for="(item2,index2) in item.son"
                    :key="index2"
                    @click="goPage(item2,item,isSex)"
                  >{{item2}}</li>
                </ul>
              </div>
            </div>
          </div>
        </van-tab>
      </van-tabs>
      <van-icon class="login" @click="login()" name="contact" size="30px" />
      <van-icon class="book" name="orders-o" size="30px" @click="bookshelf" />
    </div>
  </div>
</template>
<script>
import { createNamespacedHelpers, mapGetters } from "vuex";
const { mapState, mapMutations } = createNamespacedHelpers("bookModule");
export default {
  data() {
    return {
      value: "",
      majorData: [],
      minorData: [],
      isSex: 0,
    };
  },
  created() {
    this.isSex = this.$route.query.isSex;
    if (Object.keys(this.majorDataCache).length > 0) {
      this.majorData = this.majorDataCache;
      console.log("1");
    } else {
      console.log("2");
      this.major();
    }
  },
  computed: {
    ...mapState(["majorDataCache"]),
  },
  methods: {
    ...mapMutations(["changeHome"]),
    //返回
    onClickLeft() {
      this.$router.go(-1);
    },
    login() {
      this.$router.push({ name: "Login" });
    },
    bookshelf() {
      this.$router.push({ name: "Bookshelf" });
    },
    //获取带书籍数量的父分类
    major() {
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0,
      });
      this.axios({
        method: "GET",
        url: "https://novel.kele8.cn/categories",
      })
        .then((result) => {
          this.$toast.clear();

          if (Object.keys(result.data).length > 0) {
            this.minorData = result.data;
            this.minor();
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //获取带书籍数量的子分类
    minor() {
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0,
      });
      this.axios({
        method: "GET",
        url: "https://novel.kele8.cn/sub-categories",
      })
        .then((result) => {
          this.$toast.clear();

          if (Object.keys(result.data).length > 0) {
            //判断male为空，则删除
            this.minorData.male = this.minorData.male.slice(0, -1);
            //赋值子类给父类
            for (let i = 0; i < this.minorData.male.length; i++) {
              if (this.minorData.male[i] != "") {
                this.minorData.male[i].son = result.data.male[i].mins;
              }
            }
            //赋值子类给父类
            for (let i = 0; i < result.data.female.length; i++) {
              this.minorData.female[i].son = result.data.female[i].mins;
            }
            //判断female为空，则删除
            for (let i = 0; i < this.minorData.female.length; i++) {
              if (this.minorData.female[i].son == "") {
                this.minorData.female.splice(i, 1);
                i = i - 1;
              }
            }
            setTimeout(() => {
              this.majorData = this.minorData;
              this.changeHome({
                key: "majorDataCache",
                value: this.majorData,
              });
            }, 200);
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //跳转页面，传递两个参数：小说父分类和子分类
    goPage(item2, item, isSex) {
      let sex = "";
      if (Boolean(isSex)) {
        sex = "female";
      } else {
        sex = "male";
      }
      this.$router.push({
        name: "Classify",
        query: {
          sex: sex,
          major: item.name,
          minor: item2,
        },
      });
    },
    change(index) {
      this.isSex = index;
    },
  },
};
</script>
